.App-wrapper{
    margin : 0 auto;
    display: grid;
    width: 1800px;
    grid-template-areas:
    'h h'
    'n c';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap:10px;

}

.App-wrapper-content{
    grid-area: c;
    background-color: #F3E9DD;
}



